import React from "react";
import { DeviceData } from "./Data";

export const GetData = () => {
  return (
    <>
      <HomePageHeader />
      <div className="stock-container">
        {DeviceData.map((data, key) => {
          return (
            <div key={key}>
              <Stock
                key={key}
                deviceimg={data.deviceimg}
                devicename={data.devicename}
                devicemodel={data.devicemodel}
            
              />
            </div>
          );
        })}
      </div>
    </>
  );
};

const HomePageHeader = () => {
  return (
    <header className="header">
      <h2>Your Device Disply</h2>
    </header>
  );
};

const Stock = ({ deviceimg, devicename, devicemodel }) => {
  if (!deviceimg) return <div />;
  return (
      <div>
          <div className="card">
              <img src={deviceimg} />
              <div className="card-body">
                  <h4 className="card-title">{devicename}</h4>
                        <p class="card-text">{devicemodel}</p>
                  <a href="javascript:void(0)" class="btn btn-primary">See Profile</a>
              </div>

          </div>
      </div>
    
  )
}

export default GetData